#information {
  background-color: #050227;
}
#information .home {
  overflow-x: hidden;
  overflow-y: hidden;
}
#information .row:last-child .square {
  border-top: 1px solid #FF04FF;
  border-bottom: none;
}
#information .row .square {
  position: relative;
  transition: background-color 2s ease;
  margin: 0px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 50vh;
  color: #fff;
  padding: 4rem;
  border: 1px solid #FF04FF;
  transform: rotate(349deg);
  border-top: none;
}
#information .row .square .logo {
  transform: rotate(355deg);
}
#information .row .square#company .hover-text h1 {
  font-family: "Exo", serif;
  font-optical-sizing: auto;
  color: #fff;
  font-size: 19px;
  margin-bottom: 13px;
}
#information .row .square#company .hover-text a {
  color: #fff;
}
#information .row .square:hover {
  background-color: #4E2DF3;
}
#information .row .square:nth-child(1) {
  border-left: none;
}
#information .row .square h2,
#information .row .square h1 {
  transform: rotate(349deg);
  font-family: "Permanent Marker", serif;
  color: #FF04FF;
  font-optical-sizing: auto;
  font-weight: 700;
  letter-spacing: 0.04em;
  text-transform: lowercase;
  font-size: 33px;
  text-transform: uppercase;
}
#information .row .square h2 span,
#information .row .square h1 span {
  text-align: center;
  display: block;
  font-size: 30px;
}
@media screen and (max-width: 600px) {
  #information .row .square h2,
  #information .row .square h1 {
    font-size: 28px;
  }
}
#information .row .square p,
#information .row .square a {
  transform: rotate(349deg);
  font-family: "Exo", serif;
  font-optical-sizing: auto;
  font-size: 19px;
  font-weight: 700;
  letter-spacing: 0.04em;
  text-transform: lowercase;
  text-transform: uppercase;
}
#information .row .square span {
  font-size: 19px;
}
#information .row .square a {
  color: #fff;
}
#information .row .square .default-text {
  opacity: 1;
  transition: opacity 1.5s ease;
}
#information .row .square .hover-text {
  opacity: 0;
  position: absolute;
  transition: opacity 1.5s ease;
}
#information .row .square .hover-text.contact p {
  color: #fff;
  margin-bottom: 13px;
}
#information .row .square .hover-text p {
  color: #fff;
  margin-bottom: 13px;
}
#information .row .square:hover .default-text {
  opacity: 0;
}
#information .row .square:hover .hover-text {
  opacity: 1;
}

.other-services h2 {
  margin: 6rem 0 4rem;
  font-family: "Permanent Marker", serif;
  font-weight: 700;
  color: #5FD0E1;
  display: inline-block;
  padding-bottom: 1rem;
  border-bottom: 3px solid #5FD0E1;
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
}
.other-services h3 {
  color: #FF04FF;
  font-weight: 600;
  margin-bottom: 1.5rem;
}
.other-services .row {
  padding-bottom: 3rem;
}
.other-services .details {
  padding-left: 19px;
}
.other-services .details p {
  margin-bottom: 1rem;
}
.other-services .details p span {
  color: #FF04FF;
  border-radius: 19px;
  padding: 8px 16px 6px;
  letter-spacing: 0.06px;
  font-weight: 500;
  border-radius: 32px;
  border: 2px solid #FF04FF;
  background-color: #fff;
  display: inline-block;
  box-shadow: 5px 5px 0 0 #FF04FF;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  position: relative;
  -webkit-transition-property: color;
  transition-property: color;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
}
.other-services .details p span:before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 32px;
  background: #FF04FF;
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
  -webkit-transform-origin: 0 50%;
  transform-origin: 0 50%;
  -webkit-transition-property: transform;
  transition-property: transform;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-timing-function: ease-out;
  transition-timing-function: ease-out;
}
.other-services .details p span:hover, .other-services .details p span:focus, .other-services .details p span:active {
  color: #fff;
}
.other-services .details p span:hover:before, .other-services .details p span:focus:before, .other-services .details p span:active:before {
  -webkit-transform: scaleX(1);
  transform: scaleX(1);
}
.other-services .default-text {
  padding-bottom: 1rem;
}

.about * {
  font-family: "Exo", serif;
