.contact {
  background-color: #050227;
  padding: 3rem;
  border-radius: 20px;
}
.contact h1 {
  font-family: "Permanent Marker", serif;
  color: #FF04FF;
}
.contact label {
  color: #fff;
}
.contact .form-control {
  background-color: transparent;
  border: none;
  border-radius: 0;
  margin-bottom: 2rem;
  padding: 9px 2px 2rem;
  padding-bottom: 1rem;
  border-bottom: 2px solid #FF04FF;
  color: #fff;
}
.contact input {
  color: #fff;
}
.contact .btn-primary {
  color: #fff;
  font-family: "Permanent Marker", serif;
  font-size: 19px;
}
.contact .details p,
.contact .details a {
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: #FF04FF;
  font-family: "Exo", serif;
  font-optical-sizing: auto;
  color: #fff;
  margin-bottom: 1rem;
  font-size: 22px;
  font-weight: 700;
  letter-spacing: 2px;
}
.contact #my-form-status {
  color: #4E2DF3;
  font-family: "Exo", serif;
  font-optical-sizing: auto;
  font-size: 19px;
  margin-top: 2rem;
  display: inline-block;
  padding: 10px;
}

